import ImageCard from "../components/ImageCard";
import Input from "antd/es/input/Input";
import BerryPie from "../picture/pie_Berry.jpg";
import ChocoPie from "../picture/pie_Choco.jpg";
import EarlyPie from "../picture/pie_Early.jpg";
import MatchaPie from "../picture/pie_Matcha.jpg";
import TaroPie from "../picture/pie_Taro.jpg";

const pictures = [
  {
    title: "",
    pic: BerryPie,
    desc: "",
  },
  {
    title: "",
    pic: ChocoPie,
    desc: "",
  },
  {
    title: "",
    pic: EarlyPie,
    desc: "",
  },
  {
    title: "",
    pic: MatchaPie,
    desc: "",
  },
  {
    title: "",
    pic: TaroPie,
    desc: "",
  },
  {
    title: "",
    pic: BerryPie,
    desc: "",
  },
  {
    title: "",
    pic: ChocoPie,
    desc: "",
  },
  {
    title: "",
    pic: EarlyPie,
    desc: "",
  },
  {
    title: "",
    pic: MatchaPie,
    desc: "",
  },
  {
    title: "",
    pic: TaroPie,
    desc: "",
  },
  {
    title: "",
    pic: BerryPie,
    desc: "",
  },
  {
    title: "",
    pic: ChocoPie,
    desc: "",
  },
  {
    title: "",
    pic: EarlyPie,
    desc: "",
  },
  {
    title: "",
    pic: MatchaPie,
    desc: "",
  },
  {
    title: "",
    pic: TaroPie,
    desc: "",
  },
];

function Pie() {
  const renderedImage = pictures.map((picture, index) => {
    return (
      <ImageCard
        title={picture.title}
        description={picture.desc}
        context={picture.desc}
        picture={picture.pic}
        key={index}
      />
    );
  });

  return (
    <div>
      <form className="m-4 p-4 flex content-end">
        <label className="text-2xl"></label>
        <Input className="w-1/4" size="large" placeholder="" />
      </form>
      <div className="grid grid-cols-4 gap-3 place-items-center p-4">
        {renderedImage}
      </div>
    </div>
  );
}

export default Pie;
